* {
    margin: 0;
    padding: 0;
}

* {
    box-sizing: border-box;
}

*::after {
    box-sizing: border-box;
}

*::before {
    box-sizing: border-box;
}

#demo {
    word-break: break-all;
}

#div1 {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
}

#div1::before {
    content: "";
    display: block;
    position: absolute;
}

#div1::after {
    content: "";
    display: block;
    position: absolute;
}


@media (max-width: 500px) {
    #div1 {
        position: relative;
        margin-bottom: 20px;
        height: 40vh;
    }

    #htmlText {
        height: 60vh;
        overflow: auto;
    }

}